/**
 * Messages
 * @define message
 */
.ecl-message {
  background: transparent url("../images/svg/messages-info.svg") no-repeat 1.2rem 1.2rem;
  border: 2px solid #4073af;
  color: #404040;
  font-size: 0.89rem;
  margin-bottom: 1rem;
  min-height: 2.22rem;
  padding: 0.89rem 2.22rem 0.89rem 4.44rem;
  position: relative; }
  .ecl-message--success {
    background-image: url("../images/svg/messages-success.svg");
    border-color: #467a39; }
  .ecl-message--warning {
    background-image: url("../images/svg/messages-warning.svg");
    border-color: #fbc11d; }
  .ecl-message--error {
    background-image: url("../images/svg/messages-error.svg");
    border-color: #da2130; }
  .ecl-message--live {
    background-image: url("../images/svg/live_streaming.svg");
    border-color: #fbc11d; }

.ecl-message__title {
  font-weight: bold;
  margin-bottom: 0.89rem; }

.ecl-message__body {
  margin: 0;
  padding-left: 0.89rem; }

.ecl-message__dismiss {
  background: transparent url("../images/svg/close.svg") no-repeat center center;
  border-width: 0;
  color: transparent;
  display: block;
  font-size: 2.22rem;
  line-height: 1.22rem;
  padding: 0;
  position: absolute;
  right: 0.66rem;
  text-decoration: none;
  text-shadow: none;
  top: 0.66rem; }
  .ecl-message__dismiss:hover {
    background-image: url("../images/svg/close_hover.svg"); }

