/**
 * Ratio utilities
 * @define ratio
 */
.ecl-u-ratio-16-9 {
  position: relative;
  /* stylelint-disable-next-line */
  /* stylelint-disable-next-line */ }
  .ecl-u-ratio-16-9::before {
    content: '';
    display: block;
    padding-top: 56.25%;
    width: 100%; }
  .ecl-u-ratio-16-9 > .content {
    bottom: 0;
    left: 0;
    position: absolute;
    right: 0;
    top: 0; }

.ecl-u-ratio-4-3 {
